@import '../../themify.scss';
@import '../../theme.scss';
.animatedList {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    flex: 1;
    overflow-y: auto;
    ul {
        box-sizing: content-box;
        width: 100%;
        height: 100%;
        margin: 0;
    }
    .message.own{
        align-items: flex-end;
    }
    .message {
        display: flex;
        flex-direction: column;
        width: 100%;
        .content {
            display: flex;
            width: fit-content;
            border-radius: 4px;
            padding: 1em;
            margin: 2px 0;
            justify-content: flex-start;
            transition: all $animationTime ease;
            flex-direction: column;
            cursor: pointer;
            &:focus,
            &:active {
                outline: none;
            }
            @include themify($themes) {
                background-color: themed('alternativeBackgroundColor');
                color: themed('textColor');
                border: 1px solid themed('borderColor');
            }
            .header {
                display: flex;
                flex-direction: column;
                .user {
                    font-size: 1.1em;
                }
                .date {
                    font-size: 0.6em;
                }
            }
            .body {
                padding: 0.5em 0 0;
            }
        }
    }
}
