@import '../../themify.scss';
@import '../../theme.scss';
.chat {
    display: flex;
    flex-direction: column;
    height: 100%;
    flex: 1;

    .overlay {
        position: absolute;
        padding: 1em 0;
        float: 1px;
        @include themify($themes) {
            background-color: themed('backgroundColor');
        }
    }
    .chatInput {
        box-sizing: border-box;
        text-align: center;
        position: sticky;
        width: 100%;
        margin: auto;
        padding: 0.5em 0;
        @include themify($themes) {
            color: themed('textColor');
        }
        input {
            box-sizing: border-box;
            padding: 0 1em;
            font-size: 1em;
            height: 42px;
            border-radius: 1.5em;
            width: 100%;
            box-shadow: none;
            border: none;
            outline-width: 0;
            transition: color $animationTime ease, background-color $animationTime ease,
                border-color $animationTime ease;
            @include themify($themes) {
                background-color: themed('alternativeBackgroundColor');
                color: themed('textColor');
                border: 1px solid themed('borderColor');
            }
            &:active,
            &:focus {
                @include themify($themes) {
                    border-color: themed('primaryColor');
                }
            }
        }
        .sendButton {
            background: none;
            border-radius: 50%;
            position: absolute;
            right: 10px;
            top: 0;
            bottom: 0;
            border: none;
            cursor: pointer;
            width: 32px;
            height: 32px;
            margin: auto;
            &:hover {
                @include themify($themes) {
                    background: themed('borderColor');
                }
            }
            &:active,
            &:focus {
                outline: none;
            }
        }
    }
}
@media (max-width: 480px) {
    .chat {
        .welcome {
            margin: 1em auto 0.5em;
        }
    }
}
